<template>
  <div class="form">
    <h2> </h2>
    <select class="form__input" name="places" id="places" v-model="place" @change="getPictures">
      <option v-for="place in this.places" :key="place.id" :value="place">{{place.name}}</option>
    </select>
    <input class="form__input" v-model="place.eng" type="text" placeholder="   ">
    <input class="form__input" v-model="place.name" type="text" placeholder="   ">
    <input class="form__input" type="text" list="tags" placeholder="" v-model="place.tag">
    <datalist id="tags">
      <option value="" selected></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
    </datalist>
    <input class="form__input" type="text" list="region" placeholder="" v-model="place.region">
    <datalist id="region">
      <option value="">Abkhazia</option>
      <option value="">Sochi</option>
      <option value=" ">Krasnaia Polyana</option>
    </datalist>
    <input class="form__input" type="text" placeholder="" v-model="place.city">
    <input class="form__input" type="text" list="difficulty" placeholder="" v-model="place.difficulty">
    <datalist id="difficulty">
      <option value=1></option>
      <option value="2"></option>
      <option value="3"></option>
    </datalist>
    <input class="form__input" type="text" placeholder="" v-model="place.availability">
    <input class="form__input" type="text" placeholder="" v-model="place.time">
    <textarea class="input-rewiew__input" type="text" placeholder="" v-model="place.description"></textarea>
    <input class="form__input" type="text" placeholder="43.434954,40.442885" v-model="place.coords">
    <div class="gallery">
      <img
        v-for="picture in currentPictures"
        :key="picture"
        :src="`${$baseUrl}/img/${picture}`" 
        class="gallery-container__item"
        @click="deletePlaceImage(picture)"
      >
      <input class="new-picture" type="file" id="file" ref="file" accept="image/jpeg" @change="addPlaceImage()">
      <label v-if="place.id" for="file" class="gallery-container__item"> </label>
    </div>
    <div class="form__buttons">
      <MyButton v-if="place.id" title="" @click="editPlace"/>
      <MyButton v-if="place.id" title="" isRed="true" @click="deletePlace"/>
      <MyButton v-else title="" @click="addNewPlace"/>
    </div>
  </div>
</template>

<script>
import MyButton from './CustomComponents/MyButton.vue'

export default {
  components: {
    MyButton,
  },
  data: () => ({
    place: {},
  }),
  computed: {
    places() {
      return this.$store.state.placesModule.places
    },
    currentPictures() {
      return this.$store.state.placesModule.pictures
    },
  },
  methods: {
    addNewPlace(){
      this.$store.dispatch('addNewPlace', this.place)
    },
    editPlace(){
      delete this.place.picture
      this.$store.dispatch('editPlace', this.place)
    },
    deletePlace(){
      this.$store.dispatch('deletePlace', this.place.id)
    },
    getPictures() {
      this.$store.dispatch('getPictures', this.place.id)
    },
    addPlaceImage(){
      this.$store.dispatch("addPlaceImage", [this.place.id, this.$refs.file.files[0]])
      this.$store.dispatch("getPictures", this.place.id)
    },
    deletePlaceImage(image) {
      this.$store.dispatch("deletePlaceImage", image)
      this.$store.dispatch("getPictures", this.place.id)
    },

  },
  created() {
    this.$store.dispatch("getPlaces")
    this.$store.commit('setPictures', [])
  }
}

</script>

